.list {
    list-style: none;
    counter-reset: steps;
    font-family: 'Baloo Tamma 2', cursive;
    font-weight: 500;
	font-size: 1.2rem;
	padding-left: 10px;
}

.list li {
    counter-increment: steps;
	margin-top: 1rem;
	
}

.add-button {
    position: fixed;
    right: 15px;
    bottom: 20px;
    color: #218c74;
	font-size: 60px;
}

.button-container {
	display: flex;
	justify-content: center;
}

.button-submit {
	display: inline-flex;
  	align-items: center;
  	justify-content: center;
	background-color: #218c74;
	color: #fff;
	border-radius: 8px;
	box-shadow: 0 3px 5px rgba(0, 0, 0, 0.18);
	padding: 0.25em 0.75em;
	min-width: 10ch;
	min-height: 44px;
	margin-top: 1.1rem;
	text-align: center;
	line-height: 1.1;
	font: inherit;
	font-weight: 500;
	border: none;
	cursor: pointer;
	transition: 220ms all ease-in-out;
	outline: none;
}

.button-submit:hover,
.button-submit:active {
	background-color: #1a705d;
	transition: 220ms all ease-in-out;
}

.form-background {
	background-color: #fff;
	width: 100vw;
	height: 135px;
	z-index: 201;
	position: fixed;
	bottom: 0px;
	left: 0px;
	right: 0px;
	display: flex;
	justify-content: center;
}

.form__group {
	position: fixed;
	bottom: 16px;
	left: auto;
	padding: 15px 0 0;
	margin-top: 10px;
	margin-left: 12px;
	width: 80%;
}

.form__field {
    font-family: 'Baloo Tamma 2', cursive;
	width: 100%;
	border: 0;
	border-bottom: 2px solid #9b9b9b;
	outline: 0;
	font-size: 1.3rem;
	color: black;
	padding: 7px 0;
	background: #fff;
	transition: border-color 0.2s;
}

.form__field::-webkit-input-placeholder {
	color: #fff;
	z-index: 200;
}

.form__field::placeholder {
	color: #fff;
	z-index: 200;
}

.form__field:placeholder-shown ~ .form__label {
	font-size: 1.3rem;
	cursor: text;
	top: 20px;
}

.form__label {
	position: absolute;
	top: 0;
	display: block;
	transition: 0.2s;
	font-size: 1rem;
	color: #9b9b9b;
}

.form__field:focus {
	padding-bottom: 6px;
	font-weight: 500;
	border-width: 3px;
	border-image: linear-gradient(to right, #218c74, #38ef7d);
	border-image-slice: 1;
}

.form__field:focus ~ .form__label {
	position: absolute;
	top: 10;
	display: block;
	transition: 0.2s;
	font-size: 1rem;
	color: #218c74;
	font-weight: 700;
}

/* reset input */
.form__field:required, .form__field:invalid {
	box-shadow: none;
}

.invisible {
	display: none;
}

.background-modal {
	width: 100vw;
	height: 80vh;
	background-color: black;
	z-index: 100;
	margin: 0px;
	position: fixed;
	top: 0px;
	left: 0px;
	opacity: 0.01;
}
li {
    counter-increment: steps;
    margin-top: 1rem;
} 

.item::before {
    content: counter(steps);
    margin-right: 0.5rem;
    background: #ff6f00;
    color: white;
    width: 1.2em;
    height: 1.2em;
    border-radius: 50%;
    display: inline-grid;
    place-items: center;
    line-height: 1.4em;
}

.done::before {
    background: #218c74;
}

.done {
    text-decoration: line-through;
}

.pending::before {
    background: #ff6f00;
}

.button-delete {
	float: right;
	
} 
.header {
    font-family: 'Baloo Tamma 2', cursive;
    text-align: center;
    font-weight: 700;
    font-size: 1.5rem;
}

.logout-button {
    position: fixed;
    top: 13px;
    right: 15px;
    margin-left: 0px;
    line-height: 1.5;
    font-size: 22px;
}

.invisible {
	display: none;
}
body {
    font-family: 'Baloo Tamma 2', cursive;
    --main-color: #218c74;
}

.icon-container {
    display: flex;
    justify-content: center;
    align-content: center;
    margin-top: 5px;
    margin-bottom: 0px;
}

.icon-size {
    font-size: 100px;
    line-height: 1em;
}

.form-header {
    margin-top: 0px;
}

.form-container {
  background: #fff;
  padding: 10px 30px;
  margin-left: 10px;
  margin-right: 10px;
  border-top: 10px solid var(--main-color);
  border-left: 4px solid var(--main-color);
  border-right: 4px solid var(--main-color);
  border-bottom: 4px solid var(--main-color);
  border-radius: 20px;
}

.label, .input, textarea {
  display: block;
  width: 100%;
  font-size: 16px;
  line-height: 30px;
}

.input {
  margin-bottom: 1px;
}

.label {
    margin-top: 15px;
    padding: 0px;
    font-weight: 500;
    line-height: 1.1;
}

.error-msg-box {
    color: #b33939;
    font-style: italic;
    line-height: 1.1;
}

.input, textarea {
  border: none;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  background: #e7ecf0;
  padding-left: 5px;
  outline: none;
}

.input:focus, textarea:focus {
  border: 1px solid var(--main-color);
}

textarea {
  resize: none;
}

.invalid {
    border: 2px solid #b33939;
    background: #f6e4e4;
}

.bottom-link {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.bottom-link span {
  cursor: pointer;
  text-decoration: underline;
  margin-left: 4px;
}

.bottom-link:active {
  font-size: 18px;
}

